<template>
    <div class='book_list'>
        <div class="header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="book_list_head">文档学习</div>
            <div class='search' @click='enterSearch()'>
                <img src="../../assets/image/search.png" alt="">
            </div>
        </div>
        <v-page class="plan-list-page" style="font-size: 0.3rem;">
            <mt-loadmore ref="loadmore" class="loadmore"
            :top-method="loadTop" v-infinite-scroll="loadBottom"
            infinite-scroll-disabled="moreLoading01" infinite-scroll-distance="40" infinite-scroll-immediate-check="false" :autoFill="false">
            <div class="activity-list" ref="scrollContainer">
                <div  v-for='item in wordStudyData' :key="item.id" class='wordStudy_item' @click='enterStudy(item.id,item.name,item.specialCode,item.url,item.fileType)'>
                    <div class='wordStudycontainLeft'>
                        <div class='wordStudyName'>{{item.name}}</div>
                        <div class='wordStudyother'>
                            <!-- <div class='wordStudychapter'>
                                共{{item.cataTotalNum}}章
                            </div> -->
                            <div class='wordStudybrowse'>
                                浏览 {{item.viewNum}}
                            </div>
                        </div>
                    </div>
                    <!-- <div class="wrap">
                        <div :class="item.selfTrainEntity.schedule > 50 ? 'clip-auto circle' : 'circle'">
                        <div v-if="(item.selfTrainEntity.schedule/100)=== 1"
                        class="percent left" :style="`-webkit-transform:rotate(${360*(item.selfTrainEntity.schedule/100)}deg);clip: auto;`"></div>
                        <div v-if="(item.selfTrainEntity.schedule/100)!== 1"
                        class="percent left" :style="`-webkit-transform:rotate(${360*(item.selfTrainEntity.schedule/100)}deg);`"></div>
                        <div :class="item.selfTrainEntity.schedule > 50 ? 'percent right' : 'percent right wth0'"></div>
                        </div>
                    </div> -->
                </div>
                <div class="more_loading" style="height:0.30rem; line-height:0.30rem; text-align:center" v-show="!queryLoading01">
                    <div type="snake" color="#00ccff" :size="20" v-show="moreLoading01 && !allLoaded01">加载中...</div>
                    <div v-show="allLoaded01" style="color:#ccc">---- 看到底线了 ----</div>
                </div>
            </div>
            </mt-loadmore>
            <div class="list-empty-show" v-if="isEmpty">
                暂无数据
            </div>
        </v-page>
        <!-- 无数据显示 -->
        <div class='empty'>
            <img src="../../assets/image/空空如也.png" alt="">
            <div class='emptyWord'>空空如也~</div>
        </div>
         <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import { Indicator } from 'mint-ui'
import Loading from './../components/loading'

export default {
  name: 'index',
  components: {
    Loading
  },
  data () {
    return {
      name: '',
      specialCode: '',
      wordStudyData: [],
      isEmpty: false, // 列表查询结果是否为空
      allLoaded: false, // 列表数据是否全部加载
      topStatus: false, // 顶部下拉刷新
      queryLoading01: false,
      moreLoading01: false,
      allLoaded01: false,
      pageSize: 10,
      pageNum: 1,
      pageCount: 100,
      verDialogVisible: true // 加载页
    }
  },
  created () {
    //   this.name = localStorage.getItem("dataListName");
    //   console.log(.getItem("dataListSpecialCode"))
    this.specialCode = localStorage.getItem('dataListSpecialCode')
    // this.specialCode = this.$route.params.specialCode;
    this.getSelectMateAndSche('down')
  },
  mounted () {
  },
  methods: {
    // 下拉刷新
    loadTop () {
      this.pageNum = 1
      this.allLoaded = false
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.getSelectMateAndSche('down')
      this.$refs.loadmore.onTopLoaded()
    },
    // 上拉加载
    loadBottom () {
      if (this.allLoaded01) {
        this.moreLoading01 = true
        return
      }
      if (this.queryLoading01) {
        return
      }
      this.moreLoading01 = !this.queryLoading01
      setTimeout(() => {
        this.pageNum++
        this.getSelectMateAndSche('up')
      }, 500)
    },
    // 获取此专项类型下的书籍
    getSelectMateAndSche (flag) {
      this.queryLoading01 = false
      this.moreLoading01 = false
      this.allLoaded01 = false
      const that = this
      API.post('selectMateAndSche', {
        pageSize: this.pageSize,
        pageNum: this.pageNum,
        specialCode: this.specialCode,
        type: this.GLOBAL.TYPE.bookType
      }, 'json', (data) => {
        if (data.success) {
          if (flag === 'down') {
            this.wordStudyData = []
          }
          this.pageCount = data.data.total
          that.forBackData(data.data.records)
          that.verDialogVisible = false
        } else {
        //   console.log(data.msg);
        }
      })
    },
    // 循环返回的数据
    forBackData (list) {
      for (let i = 0; i < list.length; i++) {
        // 解析考试信息
        this.wordStudyData.push(list[i])
      }
      // this.pageCount = this.wordStudyData.length;
      // console.log(this.wordStudyData)
      this.isEmpty = false
      this.allLoaded01 = this.wordStudyData.length === this.pageCount || list.length === 1
      // console.log(this.wordStudyData.length);
      this.moreLoading01 = this.allLoaded01
      Indicator.close()
      if (this.wordStudyData.length === 0) {
        $('.empty').css('display', 'flex')
        $('.plan-list-page').css('display', 'none')
      } else {
        $('.empty').css('display', 'none')
        $('.plan-list-page').css('display', 'block')
      }
    },
    // 返回
    back () {
    // localStorage.removeItem("dataListSpecialCode")
    // localStorage.removeItem("dataListName")
      this.$router.back()
    },
    // 进入书本阅读
    enterStudy (id, name, specialCode,url,fileType) {
      API.post('selectLastContent', {
        trainMateId: id
      }, 'json', (data) => {
        if (data.success) {
          if (fileType === 'epub') {
            this.$router.push({
              name: 'text_train',
              params: { id: id, name: name, specialCode: specialCode }
            })
          } else {
            var u = navigator.userAgent // 判断是安卓还是ios
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
            var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
            if (isAndroid) {
              cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
            }
            if (isIOS) {
              cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
            }
          }
        } else {
          this.$toast('该文档不存在')
          this.loadTop()
        }
      })
    },
    // 进入搜索
    enterSearch () {
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      localStorage.setItem('enterIndex', this.GLOBAL.ENTERSOURCE.bookSearch)
      this.$router.push({
        name: 'search'
      })
    }
  }
}
</script>
<style scoped>
    .book_list{
        width:100%;
        height:100vh;
        background:#fff;
    }
    .book_list .header{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
    }
    .book_list .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .book_list .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .book_list .search{
        width:1.11rem;
        height: 0.94rem;
        position: fixed;
        top:0;
        right:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .book_list .search img{
        width:0.41rem;
        height:0.41rem;
    }
    .book_list_head{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:3rem;
        overflow-x:hidden;
        text-overflow:ellipsis;
        padding-top:0.02rem;
        white-space:nowrap;
        text-align: center
    }
    .activity-list{
        background: #fff;
        padding:0 0.35rem;
        /* margin-top:0.94rem; */
        width:100%;
        height:calc(100vh - 0.94rem);
    }
    .wordStudy_item{
        padding:0.3rem 0;
        height:1.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-top:0.012rem solid rgba(243,243,243,1);
    }
    .wordStudy_item:first-child{
        padding-top:0;
        height:1.2rem;
        border-top:0;
    }
    .wordStudycontainLeft{
        /* margin:0.3rem 0; */
        height:0.9rem;
        width:6.13rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .wordStudycontainLeft .wordStudyName{
        font-size:0.34rem;
        color:#4E4E4E;
        overflow:hidden;
        text-overflow:ellipsis;
        /* padding-top:0.03rem; */
        white-space:nowrap;
        height:0.45rem;
        line-height: 0.42rem;
    }
    .wordStudyother{
        height:0.32rem;
        display: flex;
    }
    .wordStudyother .wordStudychapter{
        background: #F2F5F9;
        padding:0 0.13rem;
        font-size:0.24rem;
        color:#A0A9B2;
        line-height: 0.36rem;
        border-radius: 0.06rem;
        height:0.32rem;
    }
    .wordStudyother .wordStudybrowse{
        font-size: 0.26rem;
        color:#9FA7AF;
        line-height: 0.32rem;
        /* margin-left: 0.26rem; */
    }
    /* 圆环样式  */
    .wrap{
        background-color:#F9F9F9;
        position: relative;
        width: 0.3rem;
        height: 0.3rem;
        border-radius: 50%;
    }
    .circle{
       clip:rect(0, 0.3rem, 0.3rem, 0.15rem);
        position: absolute;
        width: 0.3rem;
        height: 0.3rem;
        border-radius: 50%;
    }
    .percent{
        /* box-sizing: border-box;
        top:-0.01rem; left:-0.01rem; */
        position: absolute;
        width: 0.3rem;
        height: 0.3rem;
        border-radius: 50%;
    }
    .left{
        /* border: 20px solid #3C8CFF;  */
        background: #52CB97;
        clip: rect(0, 0.15rem, 0.3rem,0);
    }
    .right{
        /* border: 20px solid #3C8CFF;  */
        background: #52CB97;
        clip: rect(0, 0.3rem, 0.3rem, 0.15rem);
    }
    .wth0{width:0;}

    .clip-auto{
        clip:rect(auto, auto, auto, auto);
    }
    .empty{
        margin-top:2.75rem;
        display: none;
        flex-direction: column;
        width:100%;
        align-items: center;
    }
    .empty img{
        width:2.91rem;
        height:2.63rem;
    }
    .empty .emptyWord{
        margin-top:0.4rem;
        color:#9FA7AF;
        font-size:0.32rem;
    }
    .mint-loadmore{
        max-height: calc(100vh - 0.94rem);
        overflow-y: auto;
        margin-top: 0.94rem;
        background: #fff;
        /* -webkit-overflow-scrolling: touch; */
    }
</style>



